import Styled from "styled-components";
export const Educations = Styled.ul`



`;

export const Education = Styled.li`
margin-bottom: 10px;
`;

export const EducationMain = Styled.div`
  height: 0;
  overflow: hidden;
  margin-top: 5px;
  transition: all 0.5s linear;
`;

export const EducationTitle = Styled.h4`
position: relative;
  cursor: pointer;
  background: #f2f3f7;
  border: 1px solid #e6e6e6 !important;
  color: #333333;
  padding: 20px 25px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 1px;
  transition: all 0.3s ease-in-out;
  &:after{
    content: "+";
    position: absolute;
    right: 20px;
    font-size: 16px;
    font-weight: 600;
    top: 50%;
    line-height: 1;
    margin-top: -7px;
  }
  ${(props) =>
    props.isOpen &&
    `
    background-color: #2c98f0;
    color: white !important;
    &::after{
        content: "-";
    }
    & ~ ${EducationMain}{
        display: inline-block;
        padding: 1.5em;
        height: auto;
        border: 1px solid #e6e6e6;
  `}
`;

export const MainTitle = Styled.p`
width: 33.3%;
  float: left;
  padding-right: 10px;
  @media screen and (max-width: 540px){
    float: none;
    width: auto;
    padding: 20px 10px;
    text-align: center;
  }
`;

export const MainText = Styled.p`
width: 66.6%;
  float: right;
  @media screen and (max-width: 540px){
    width: auto;
    float: none;
    padding: 20px 10px;
    text-align: center;
  }
`;
