import React from "react";
import {
  CommonHead,
  CommonSection,
  CommonSubHead,
  CommonTextTitle,
} from "../../utils/CommonStyles";
import * as Styled from "./styled";

const Experience = () => {
  const showExperience = (experience) => {
    return (
      <Styled.Card color={experience.color}>
        <Styled.CardHeader color={experience.color}>
          <Styled.CardImg />
          <Styled.CardHeaderText>{experience.title}</Styled.CardHeaderText>
        </Styled.CardHeader>
        <Styled.CardText>{experience.text}</Styled.CardText>
      </Styled.Card>
    );
  };
  return (
    <CommonSection>
      <CommonHead>EXPERIENCE</CommonHead>
      <CommonSubHead>저는 이런 "경험"들도 해봤습니다.</CommonSubHead>
      <CommonTextTitle>
        "프로젝트를 진행하면서 새로운 기술을 적극 도입해본 경험이 있습니다."
      </CommonTextTitle>
      <Styled.Cards>{experiences.map(showExperience)}</Styled.Cards>
    </CommonSection>
  );
};

const experiences = [
  {
    title: "Atomic Design",
    text: `팀원들과 컴포넌트 구조에 대해서 고민중에 아토믹 디자인을 적용하게 되었습니다. Atom, Molecule등 계층구조를 만들어서 컴포넌트의 재사용성을 높일 수 있었습니다.`,
    color: "#fc7047",
  },
  {
    title: "프레임워크 없이 SPA구현",
    text: `React와 같은 프레임워크 없이 자바스크립트 만으로 SPA를 구현해본 경험이 있습니다. 라우터부터, 상태관리까지 자바스크립트로 구현을 하면서 프레임워크의 기능을 더 깊이 이해하게 되었습니다.`,
    color: "#efd81d",
  },
  {
    title: "WebRTC",
    text: `화상 채팅 기능을 구현하기 위해서 WebRTC를 사용해본 경험이 있습니다. Mesh, SFU, MCU등 다양한 방식의 WebRTC방식에 대한 조사를 하였고 적합한 방식을 프로젝트에 적용하였습니다.`,
    color: "#009437",
  },
  {
    title: "Next.js로 SSR 구현",
    text: `화상 채팅 기능을 구현하기 위해서 WebRTC를 사용해본 경험이 있습니다. Mesh, SFU, MCU등 다양한 방식의 WebRTC방식에 대한 조사를 하였고 적합한 방식을 프로젝트에 적용하였습니다.`,
    color: "#1d262d",
  },
  {
    title: "Typescript",
    text: `Typescript를 적용하여 팀 프로젝트에서 서로의 컴포넌트를 재사용하는데 문제를 줄일 수 있었습니다. Props에 타입을 만들어 안정성을 향상 시킬 수 있었습니다.`,
    color: "#177ef7",
  },
];

export default Experience;
