import React, { useState } from "react";
import { CommonHead, CommonSection } from "../../utils/CommonStyles";
import * as Styled from "./styled";

const Education = () => {
  const [item1, setItem1] = useState(false);
  const [item2, setItem2] = useState(false);
  return (
    <CommonSection>
      <CommonHead>EDUCATION</CommonHead>
      <Styled.Educations>
        <Styled.Education>
          <Styled.EducationTitle
            isOpen={item1}
            onClick={() => {
              setItem2(false);
              setItem1((prev) => !prev);
            }}
          >
            CHUNH-ANG UNIVERSITY
          </Styled.EducationTitle>
          <Styled.EducationMain>
            <Styled.MainTitle>
              중앙대학교 컴퓨터공학 전공 <br />
              2015.03 - 2021.08
            </Styled.MainTitle>
            <Styled.MainText>
              4년간 컴퓨터공학을 전공하며 컴퓨터공학의 기초를 학습하였습니다.
              자료구조, 네트워크, 알고리즘 등 전공 필수과목들을 수행하였고
              캡스톤디자인, 웹서비스와 같은 과목들을 수강하며 웹 프로젝트 경험을
              쌓았습니다. 또한 알고리즘 학술 동아리에서 UCPC와 같은 대회에
              참가하기도 하였습니다.
            </Styled.MainText>
          </Styled.EducationMain>
        </Styled.Education>
        <Styled.Education>
          <Styled.EducationTitle
            isOpen={item2}
            onClick={() => {
              setItem1(false);
              setItem2((prev) => !prev);
            }}
          >
            BOOSTCAMP 6TH
          </Styled.EducationTitle>
          <Styled.EducationMain>
            <Styled.MainTitle>
              부스트캠프 웹과정 6기 <br />
              2021.07 - 2021.12
            </Styled.MainTitle>
            <Styled.MainText>
              웹개발 과정 5개월을 수강하였습니다. 2주 간격으로 디자인과 기능
              명세서가 주어지며 해당 프로젝트를 개발하였습니다. 이 과정에서
              React와 같은 프레임워크를 사용하지 않고 Javascript만으로 SPA를
              개발하기도 하였습니다. 또한 프론트엔드만이 아닌 백엔드도 함께
              개발하고 배포하는 과정까지 직접 경험해보았습니다. 마지막 6주간의
              팀프로젝트에선 자유 주제로 4명의 팀원과 개발을 진행하기도
              했습니다.
            </Styled.MainText>
          </Styled.EducationMain>
        </Styled.Education>
      </Styled.Educations>
    </CommonSection>
  );
};

export default Education;
