import Styled from "styled-components";

export const Languages = Styled.ul`
display: flex;
  flex-wrap: wrap;

`;

export const Language = Styled.li`
width: 50%;
  padding: 0px 10px;
  margin-bottom: 10px;
  @media screen and (max-width: 1080px) {
    width: 100%;
    
  }
`;

export const LanguageHead = Styled.h4`
font-size: 16px;
  font-family: "Quicksand", Arial, sans-serif;
  margin-bottom: 10px;
  color: #000;
  font-weight: 600;

`;

export const Progress = Styled.div`
  height: 6px;
  box-shadow: none;
  background: #f2f3f7;
  overflow: visible;
  margin-bottom: 20px;
  border-radius: 4px;

`;

export const ProgressBar = Styled.div`
position: relative;
  height: 100%;
  width: ${(props) => props.width}%;
  background: ${(props) => props.color};
  overflow: visible;
  box-shadow: none;
  font-size: 12px;
  line-height: 1.2;
  color: #000;
  font-weight: 600;
  text-align: right;
  &::after{
    position: absolute;
    top: -2px;
    right: 0;
    width: 10px;
    height: 10px;
    content: "";
    border-radius: 50%;
    background: ${(props) => props.color};
  }
`;

export const ProgressPercentage = Styled.span`
    position: absolute;
    top: -22px;
    right: 0;
    color: ${(props) => props.color};
`;

export const LanguageText = Styled.p`
  font-size: 17px;
  line-height: 1.8;
`;
