import React from "react";
import * as Styled from "./styled";
import ProfileImg from "./profile.jpg";

const SideBar = () => {
  return (
    <Styled.Container>
      <Styled.ProfileContainer>
        <Styled.ProfileImg src={ProfileImg} alt="안승재 이미지" />
        <Styled.ProfileText>
          <Styled.ProfileLink href="#">
            <Styled.ProfileName>SeungJae Ahn</Styled.ProfileName>
          </Styled.ProfileLink>
          <Styled.ProfileField href="#">FRONTEND DEVELOPER</Styled.ProfileField>
        </Styled.ProfileText>
      </Styled.ProfileContainer>
      <Styled.Tags>
        <Styled.Tag>
          <Styled.TagLink href="#">ABOUT ME</Styled.TagLink>
        </Styled.Tag>
        <Styled.Tag>
          <Styled.TagLink href="#">TECH SKILL </Styled.TagLink>
        </Styled.Tag>
        <Styled.Tag>
          <Styled.TagLink href="#">EXPERIENCE</Styled.TagLink>
        </Styled.Tag>
        <Styled.Tag>
          <Styled.TagLink href="#">PROJECT</Styled.TagLink>
        </Styled.Tag>
        <Styled.Tag>
          <Styled.TagLink href="#">EDUCATION</Styled.TagLink>
        </Styled.Tag>
      </Styled.Tags>
    </Styled.Container>
  );
};

export default SideBar;
