import React from "react";
import * as Styled from "./styled";
import { CommonHead, CommonSection } from "../../utils/CommonStyles";
import project1Img from "./menu.png";
import project2Img from "./main.png";
import project3Img from "./project3.png";
const Project = () => {
  const showProject = (project) => {
    return (
      <Styled.Project>
        <Styled.ProjectOverlay img={project.img} />
        <Styled.ProjectDesc>
          <Styled.ProjectTitle>{project.title}</Styled.ProjectTitle>
          <Styled.ProjectText>{project.text}</Styled.ProjectText>
          <Styled.Tag href={project.pdf} target="_blank">
            프로젝트 상세보기
          </Styled.Tag>
        </Styled.ProjectDesc>
      </Styled.Project>
    );
  };

  return (
    <CommonSection>
      <CommonHead>PROJECT</CommonHead>
      <Styled.Projects>{projects.map(showProject)}</Styled.Projects>
    </CommonSection>
  );
};

const projects = [
  {
    title: "배달공유 플랫폼!(2019.03~2019.06)",
    text: `근처의 음식 주문을 원하는 사람들을 연결하여 함께 주문함으로써 배달비와 최소주문 금액 부담을 줄일 수 있도록 하는 서비스입니다.
     저는 식당에서 사용하는 주문관리 프로그램을 Vue.js&Electron을 이용하여 개발하였습니다.`,
    img: project1Img,
    pdf: "project1.pdf",
  },
  {
    title: "커스텀 게더타운(21.10.11~21.10.22)",
    text: `실제 게더타운을 모방하여 필수적인 기능들을 직접 구현해보는 프로젝트
    실시간으로 사용자의 캐릭터 움직임과, 채팅, 화상캠을 Socket과 WebRTC기술을 사용하여 구현하였습니다. React를 사용하여 컴포넌트 기반으로 구현하며 전역 상태관리 방식으로 Recoil을 사용하였습니다.
    `,
    img: project2Img,
    pdf: "project2.pdf",
  },
  {
    title: "개발자를 위한 실시간 Q&A 서비스(21.10.25~21.12.05)",
    text: `Stackoverflow와 같은 개발자 Q&A와 실시간 답변 기능
    개발자의 질문 답변을 위한 커뮤니티 구현. 또한 음성, 동시 편집 기능을 통해 실시간으로 답변을 할 수 있는 기능이 있습니다.
    `,
    img: project3Img,
    pdf: "project3.pdf",
  },
];
export default Project;
