import React from "react";
import {
  CommonHead,
  CommonSection,
  CommonSubHead,
} from "../../utils/CommonStyles";
import * as Styled from "./styled";

const Skill = () => {
  const showLanguage = (language) => {
    return (
      <Styled.Language>
        <Styled.LanguageHead>{language.head}</Styled.LanguageHead>
        <Styled.Progress>
          <Styled.ProgressBar
            color={language.color}
            width={language.percentage}
          >
            <Styled.ProgressPercentage color={language.color}>
              {language.percentage}%
            </Styled.ProgressPercentage>
          </Styled.ProgressBar>
        </Styled.Progress>
        <Styled.LanguageText
          dangerouslySetInnerHTML={{ __html: language.text }}
        ></Styled.LanguageText>
      </Styled.Language>
    );
  };
  return (
    <CommonSection>
      <CommonHead>TECH SKILLS</CommonHead>
      <CommonSubHead>"저는 지금 이것을 할 수 있습니다"</CommonSubHead>
      <Styled.Languages>{languages.map(showLanguage)}</Styled.Languages>
    </CommonSection>
  );
};
const languages = [
  {
    head: "JAVASCRIPT",
    percentage: 90,
    color: "#f9bf3f",
    text: `
          동작원리부터 문법까지 깊이 이해하고자 했습니다.
          <br />
          <strong
              >ES6문법과 이전 문법의 차이점을 이해하고 브라우저에서
              Javascript가 어떻게 동작하는지까지 철저히
              이해했습니다.</strong
          >
          <br />
          부스트캠프 교육과정에서 React와 같은 프레임워크 없이
          자바스크립트 만으로 SPA를 구현하면서 프레임워크가 어떻게
          동작하는지 더 깊이 이해할 수 있었습니다.
          `,
  },
  {
    head: "REACT.JS",
    percentage: 70,
    color: "#2c98f0;",
    text: `
      “리액트를 어떻게하면 더 잘 쓸 수 있을까?”를 계속 고민하며
      프로젝트를 했습니다. 프론트엔드 개발을 편리하게 해주지만 쓸수록
      잘 쓰는 것은 어렵다는 생각을 했습니다.
      <br />
      <strong
        >상태관리를 어떻게 하면 적절할지, 컴포넌트를 어떻게 분리하면
        좋을지, 불필요한 렌더링을 줄일 수 는 없을지 생각해야 할 것들이
        많은 것을 깨닫게 됐습니다.</strong
      >
      제 부족함을 알고 꾸준히 부족한 부분을 채워나가고 있습니다.
          `,
  },
  {
    head: "HTML5",
    percentage: 80,
    color: "#ec5453",
    text: `
      div태그만이 아닌
      <strong
        >시맨틱 태그를 사용하여 HTML을 작성할 수 있습니다.</strong
      >
      웹 접근성 관련된 강의를 들으면서 기존에 무심코 넘겼던 HTML
      작성법에 대해서 보완을 하였습니다.
          `,
  },
  {
    head: "CSS",
    percentage: 70,
    color: "#4054b2",
    text: `
      제가 생각했던 레이아웃을 무너지지 않게 만들 수 있습니다. 브라우저 크기, 컨텐츠 크기에 따라 문제가 생겼을
      때 원인조차 찾지 못했던 것에 괴로워했던 경험이 있습니다.
      <br />이후로 연습을 통해
      <strong
        >CSS에서 레이아웃 만큼은 깨지지 않는 CSS를 작성할 수
        있습니다.</strong
      >
       그리고 화려한 애니메이션을 작성하진 못하더라도 반응형 레이아웃을 작성할
      수 있게 되었습니다.
          `,
  },
  {
    head: "EXPRESS",
    percentage: 50,
    color: "#a84cb8",
    text: `
      Express를 통해 프로젝트에서 Javascript로 백엔드를 개발할 수
      있었습니다.
      <strong
        >간단한 CRUD와, 로그인 방식을 구현해보면서 웹서비스 전체의
        흐름을 더 잘 이해할 수 있었습니다.</strong
      >
      <br />
      Express의 특징인 유연한 구조는 저에게 모듈을 분리하는 방식에
      대한 고민을 해볼 수 있게 했습니다.
          `,
  },
];

export default Skill;
