import Styled from "styled-components";

export const Container = Styled.aside`
    float: left;
    position: fixed;
    width: 300px;
    height: 100vh;
    padding: 40px 0px;
    overflow-y: auto;
    background-color: #f2f3f7;
    transition: all 0.5s ease-in-out;
    @media screen and (max-width: 768px){
        transform: translateX(-300px);
        z-index: 999;
    }
`;

export const ProfileContainer = Styled.div`
display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

`;

export const ProfileImg = Styled.img`
display: block;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 30px;
`;

export const ProfileText = Styled.div`
margin-bottom: 20px;
`;

export const ProfileLink = Styled.a`


`;

export const ProfileName = Styled.h1`
font-family: Georgia, serif;
  color: #000;
  font-weight: 600;
  font-size: 20px;
`;

export const ProfileField = Styled.a`
font-size: 12px;
  color: #2c98f0;
`;

export const Tags = Styled.ul`
text-align: center;
`;

export const Tag = Styled.li`
margin-bottom: 15px;
`;

export const TagLink = Styled.a`
position: relative;
  color: #333;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.8;
  padding: 15px 0px;
  &:after{
    content: "";
    position: absolute;
    height: 1px;
    bottom: 7px;
    left: 0;
     right: 0;
    background-color: #2c98f0;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s;
    box-sizing: border-box;
  }
  &:hover{
    color: black;
  }
  &:hover:after{
    visibility: visible;
    transform: scaleX(1);
  }
`;
